const existingIds = {}

// Generates ID without collisions.
export const generateId = () => {
    const id = Math.random().toString(36).substr(2, 9)
    if (existingIds[id]) {
        return generateId()
    }
    existingIds[id] = true
    return id
}

const applyId = (obj, key) => { obj[key] = generateId() }

export const applyIdToArray = (arr, key = 'itemId', recurseKey) => {
    return arr.map(item => {
        applyId(item, key)
        if (recurseKey && item[recurseKey]) {
            applyIdToArray(item[recurseKey])
        }
        return item
    })
}

export const createSlug = (str = '', delimiter = '-') => str.toLowerCase().replace(/[^\w ]+/g, '').replace(/ +/g, delimiter)
