class IDXStrapClass {
    constructor ({
        prefix = 'idx',
        separator = '-',
        applyPrefix = false
    } = {}) {
        this.name = 'idxStrap'
        this.prefix = this.makePrefix(prefix, separator, applyPrefix)
        this.applyPrefix = this.prefix.length > 0
    }

    makePrefix (prefix, separator, applyPrefix) {
        if (applyPrefix && prefix.length) {
            return prefix + separator
        }
        return ''
    }
}

export {
    IDXStrapClass as default
}
